.vanilla-calendar-style{
  display: none;
  position: absolute;
  z-index: 9999;
  background: #fff;
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.38);
  border-radius: 8px;
  border: none;
  padding: 32px 40px;
  @media screen and (max-width: 470px){
    padding: 32px 17px;
  }
  .vanilla-calendar-month, .vanilla-calendar-year {
    font-family: Gilroy-Bold,sans-serif;
    font-size: 16px;
    line-height: 24px;
    color: #000;
    &:hover{
      color:#A3D562;
    }
  }
  .vanilla-calendar-month {
    padding: 2px 5px 2px 2px;
  }
  .vanilla-calendar-arrow {
    width: 2rem;
    height: 2rem;
    &:before, &:after{
      display: none;
    }
  }
  .vanilla-calendar-arrow_prev {
    transform: rotate(0);
    background: url(../img/calendar-arrow-left.svg) no-repeat 50%;
  }
  .vanilla-calendar-arrow_next {
    background: url(../img/calendar-arrow-left.svg) no-repeat 50%;
    transform: rotate(180deg);
  }
  .vanilla-calendar-week {
    border-bottom: 1px solid #dedede;
    padding-bottom: 7px;
    margin-bottom: 15px;
  }
  .vanilla-calendar-week__day {
    font-family: Gilroy-Medium,sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #737373;
    font-weight: 400;
    text-transform: lowercase;
  }
  .vanilla-calendar-day__btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .vanilla-calendar-day__btn {
    font-size: 16px;
    font-weight: 400;
    color: #000;
    font-family: Gilroy-Medium,sans-serif;
    width: 37px;
    height: 37px;
    border-radius: 6px;
    &:hover {
      background: #c9e3aa;
    }
  }
  .vanilla-calendar-day__btn_selected, .vanilla-calendar-day__btn_selected.vanilla-calendar-day__btn_intermediate, .vanilla-calendar-day__btn_weekend.vanilla-calendar-day__btn_selected, .vanilla-calendar-day__btn_weekend.vanilla-calendar-day__btn_selected.vanilla-calendar-day__btn_intermediate {
    background-color: #8bc540;
    color: #000;
    &:hover {
      background: #c9e3aa;
      color: #000;
    }
  }
  .vanilla-calendar-day__btn_today{
    color: #000;
    background-color: #fff;
    border: 1px solid #8bc540;
    &:hover {
      background: #c9e3aa;
      color: #000;
    }
  }
  .vanilla-calendar-day__btn_prev,.vanilla-calendar-day__btn_next{
    color: #a9a9a9;
  }
  &.active{
    display: block;
  }
  .vanilla-calendar-years__year,.vanilla-calendar-months__month{
    font-weight: normal;
    font-family: $ff-semi-bold;
    color: #000;
    background-color: #8bc540;
    &:hover{
      background-color: #c9e3aa;
    }
  }
  .vanilla-calendar-years{
    max-height: 144px;
    overflow: hidden;
  }


}
