.notification {
  background-color: $color-bg-white;
  border-radius: .75rem;
  bottom: 1.5rem;
  box-shadow: 0px 10px 30px rgba(202, 204, 223, 0.5);
  max-width: 25rem;
  opacity: 0;
  padding: 1.5rem;
  pointer-events: none;
  position: fixed;
  transition: all .3s ease-in-out;
  right: 1.5rem;
  z-index: 111;

  @include respondTo(mobile) {
    bottom: 1.25rem;
    max-width: calc(100vw - 2.5rem);
    padding: 1.25rem;
    right: 1.25rem;
  }

  &.show {
    opacity: 1;
    pointer-events: auto;
  }

  &__content {
    color: $color-text;
    display: flex;
    font-family: $ff-medium;
    font-size: .875rem;
    line-height: 1.29;

    a {
      text-decoration: underline $color-text-gray-2;
      text-decoration-thickness: 2px;
      text-underline-offset: .25rem;
      transition: .3s color;

      &:hover {
        color: $color-text-accent;
      }
    }

    &_success {

      &::before {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='12' cy='12' r='12' fill='%238BC540'/%3E%3Cpath d='M8.72139 12.3075C8.33891 11.9091 7.70588 11.8961 7.30747 12.2786C6.90906 12.6611 6.89614 13.2941 7.27861 13.6925L8.72139 12.3075ZM10.88 16L10.1586 16.6925C10.3708 16.9136 10.6727 17.0252 10.9777 16.9952C11.2827 16.9653 11.5571 16.7972 11.7223 16.5391L10.88 16ZM16.8423 8.53905C17.14 8.07388 17.0042 7.45544 16.5391 7.15773C16.0739 6.86002 15.4554 6.99577 15.1577 7.46095L16.8423 8.53905ZM7.27861 13.6925L10.1586 16.6925L11.6014 15.3075L8.72139 12.3075L7.27861 13.6925ZM11.7223 16.5391L16.8423 8.53905L15.1577 7.46095L10.0377 15.4609L11.7223 16.5391Z' fill='white'/%3E%3C/svg%3E%0A");
        content: '';
        display: block;
        height: 1.5rem;
        margin-right: .75rem;
        min-width: 1.5rem;
        width: 1.5rem;
      }
    }
  }
}
